import React, { useState } from "react";
import _ from "lodash";

const button = {
  width: "10%",
  height: 50,
  fontWeight: "bold",
  borderRadius: 10,
  fontSize: 18,
  backgroundColor: "#075e54",
  borderWidth: 0,
  color: "#fff",
  margin: 10,
};

export default function UserLogin({ setUser }) {
  const [user, setAUser] = useState("");

  //   
  function handleSetUser() {
    if (!user) {
      //   
      console.log("  ");
    }
    // Props      localStorage 
    localStorage.setItem("user", user);
    setUser(user);
    localStorage.setItem("avatar", `https://picsum.photos/id/${_.random(1, 500)}/200/300`);
  }

  return (
    <div>
      <h1 style={{ margin: 10, textAlign: "center" }}>Super Chat </h1>

      <div style={{ display: "flex", justifyContent: "center" }}>
        <input style={{ margin: 10, height: 30, width: "25%", borderRadius: 10, borderWidth: 10, fontSize: 15, paddingInline: 5 }} value={user} onChange={(e) => setAUser(e.target.value)} placeholder="  ."></input>
        <button onClick={() => handleSetUser()} style={button}>
          
        </button>
      </div>
    </div>
  );
}
